export default ":root {\r\n\r\n      /* \r\n      全局使用主题色步骤：\r\n\r\n      1. 项目public文件夹新建themes.css\r\n\r\n      2. Copy此文件内容到themes.css\r\n\r\n      3. 在index.html的头部引入此文件\r\n         <link rel=\"stylesheet\" type=\"text/css\" href=\"./themes.css\" rel=\"external nofollow\">\r\n\r\n      4. 页面样式中使用\r\n         color: var(--color-theme) \r\n      \r\n      5. 亦可通过其他方式使用\r\n      */\r\n\r\n\r\n\r\n\r\n    /* 以下色调来自于设计规范 */\r\n  \r\n    /** 品牌色 \r\n      奥迪品牌的颜色是相同的地位,总是出现在全调,前进红不能变暗、变淡或透明地显示。\r\n  */\r\n  \r\n    --color-default: #000000; /*黑色*/\r\n    --color-white: #ffffff; /*白色*/\r\n    --color-theme: #f50537; /*前进红*/\r\n  \r\n    /*  实用灰色调 \r\n      对于功能应用,调色板可以扩展到包括从白色到黑色的灰色色调。\r\n  */\r\n    --color-grey-5: #f2f2f2; /*灰-5*/\r\n    --color-grey-10: #e5e5e5; /*灰-10*/\r\n    --color-grey-15: #d9d9d9; /*灰-15*/\r\n    --color-grey-20: #cccccc; /*灰-20*/\r\n    --color-grey-30: #b3b3b3; /*灰-30*/\r\n    --color-grey-40: #999999; /*灰-40*/\r\n    --color-grey-50: #808080; /*灰-50*/\r\n    --color-grey-60: #666666; /*灰-60*/\r\n    --color-grey-70: #4c4c4c; /*灰-70*/\r\n    --color-grey-80: #333333; /*灰-80*/\r\n    --color-grey-10: #1a1a1a; /*灰-90*/\r\n  \r\n    /*  表格/控件辅助色 \r\n      大规模选择状态使用\r\n  */\r\n    --color-neon-5: #f4f7ff; /*霓虹蓝-5*/\r\n    --color-neon-10: #e9efff; /*霓虹蓝-10*/\r\n    --color-neon-15: #dee7ff; /*霓虹蓝-15*/\r\n    --color-neon-20: #d3e0ff; /*霓虹蓝-20*/\r\n    --color-neon-30: #bed0ff; /*霓虹蓝-30*/\r\n    --color-neon-40: #a8c0ff; /*霓虹蓝-40*/\r\n    --color-neon-50: #92b0fe; /*霓虹蓝-50*/\r\n    --color-neon-60: #7ca1fe; /*霓虹蓝-60*/\r\n    --color-neon-70: #6691fe; /*霓虹蓝-70*/\r\n    --color-neon-80: #5181fe; /*霓虹蓝-80*/\r\n    --color-neon-10: #3b72fe; /*霓虹蓝-90*/\r\n  \r\n    /*  信号色 \r\n      奥迪的信号颜色仅用于通知,警告,和状态信息,如果需要一个颜色区分,红色可用于块,而黄色和绿色可能只用于排版和图标。\r\n  */\r\n    --color-info-red: #eb0d3f; /*信号红*/\r\n    --color-info-green: #eb0d3f; /*信号绿*/\r\n    --color-info-yellow: #0da20d; /*信号黄*/\r\n    --color-info-red-d: #fd2c4e; /*信号红-暗*/\r\n    --color-info-green-d: #ffaa00; /*信号绿-暗*/\r\n    --color-info-yellow-d: #ffaa00; /*信号黄-暗*/\r\n  \r\n    /*  充电色 \r\n      奥迪的充电颜色只用于电动汽车上下文—无论是作为用户界面的一部分,还是显示主动充电过程.充电的颜色只能有选择性和节制地使用。\r\n  */\r\n    --color-energy-blue: #2562fe; /*霓虹蓝*/\r\n    --color-energy-green: #0da20d; /*荧光绿*/\r\n    --color-energy-blue-d: #556efe; /*霓虹蓝-暗*/\r\n    --color-energy-green-d: #15da15; /*荧光绿-暗*/\r\n  }\r\n  ";