export default "<template>\r\n  <div class=\"checkbox-view\">\r\n    <el-checkbox\r\n        v-model=\"checked\"\r\n        @change=\"checkTap\"\r\n        >选我</el-checkbox\r\n    >\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      checked: true,\r\n    };\r\n  },\r\n  methods: {\r\n    checkTap(val) {\r\n        console.log(\"我改变了!\",val);\r\n    },\r\n  },\r\n};\r\n</script>\r\n<style scoped>\r\n/*  \r\n  样式穿透（后两种需要scss支持）\r\n  >>>  \r\n  :deep()  \r\n  /deep/    \r\n*/\r\n.checkbox-view {\r\n    display: flex;\r\n    padding: 30px;\r\n}\r\n\r\n/* checkbox */\r\n.checkbox-view >>> .el-checkbox__input.is-checked .el-checkbox__inner {\r\n  background-color: #f50537;\r\n  border-color: #f50537;\r\n}\r\n.checkbox-view >>> .el-checkbox__input.is-indeterminate .el-checkbox__inner {\r\n  background-color: #f50537;\r\n  border-color: #f50537;\r\n}\r\n.checkbox-view >>> .el-checkbox__inner {\r\n  width: 24px;\r\n  height: 24px;\r\n  border-radius: 2px;\r\n  font-size: 18px;\r\n  background: #fff;\r\n  border-color: #dcdfe6;\r\n}\r\n\r\n/* label样式 */\r\n:deep() .el-checkbox__label ,\r\n:deep() .el-checkbox__input.is-checked+.el-checkbox__label {\r\n    color: #1A1A1A;\r\n}\r\n\r\n/* focus border-color */\r\n:deep() .el-checkbox__input.is-focus .el-checkbox__inner {\r\n  border-color: #f50537;\r\n}\r\n\r\n/* 勾选状态：对勾样式 */\r\n:deep()  .el-checkbox__input.is-checked .el-checkbox__inner,\r\n.el-checkbox__input.is-indeterminate .el-checkbox__inner {\r\n  background-color: #fff;\r\n  border-color: #fff;\r\n}\r\n:deep()  .el-checkbox__inner::after {\r\n  border: 2px solid #fff;\r\n  border-left: 0px;\r\n  border-top: 0px;\r\n  width: 5px;\r\n  height: 9px;\r\n  left: 8px;\r\n  position: absolute;\r\n  top: 4px;\r\n}\r\n/* checkbox hover */\r\n:deep()  .el-checkbox__inner:hover {\r\n  border-color: #f50537;\r\n}\r\n\r\n</style>\r\n";