<template>
  <div class="container">
    <headera-bar></headera-bar>

    <div class="homepage">
      <div class="asider-view">
        <asider-bar
          @sideChange="sideChange"
          :componentList="componentList"
        ></asider-bar>
      </div>

      <div class="content-view">
        <div class="component-view">
          <p class="component-view-title" v-if="itemList.title">
            {{ itemList.title }}
          </p>
          <div
            v-for="(item, index) in itemList.list"
            :key="index"
            @click="itemTap(item, index)"
            :class="[
              'component-item',
              selectCom.componentName == item.componentName
                ? 'component-active'
                : '',
            ]"
          >
            <template v-if="item.componentName">
              <component
                :is="item.componentName"
                :id="item.componentName"
              ></component>
            </template>
          </div>
        </div>

        <div class="code-view">
          <codeView :value="curCode" @change="codeChange"></codeView>
        </div>
      </div>

      <!-- <div class="attr-view">
      <h6>属性</h6>
    </div> -->
    </div>
  </div>
</template>
<script>
import codeMixin from "./mixin.js";
import headeraBar from "@/mycoms/header-bar.vue";
import asiderBar from "@/mycoms/asider-bar.vue";
import codeView from "@/mycoms/code-view.vue";

export default {
  mixins: [codeMixin],
  components: {
    codeView,
    headeraBar,
    asiderBar,
  },
  data() {
    return {
      itemList: {}, //子分类对象
      selectInd: 0,
      selectCom: {},
      codeh: 800,
      curCode: "<template></template>",
    };
  },
  created() {
    this.init();
  },
  mounted() {
    const h =
      document.documentElement.clientHeight || document.body.clientHeight;
    this.codeh = h; //- 60
  },
  methods: {
    init() {
      this.selectInd = 0;
      this.itemList = this.componentList[0].list[0];
      this.selectCom = this.itemList.list[0];
      const str = this.selectCom.componentName + "Code";
      this.curCode = this[str];
    },
    sideChange(e) {
      console.log("sideChange>>>", e);
      this.itemList = e.it;
      this.selectInd = 0;
      this.selectCom = e.it.list[0];
      const str = this.selectCom.componentName + "Code";
      this.curCode = this[str];
    },

    // 组件选中
    itemTap(it) {
      this.selectCom = it;
      console.log(it);
      const str = `${it.componentName}Code`;
      this.curCode = this[str] || "<template></template>";
    },

    // 代码变动
    codeChange(value) {
      console.log(999, value);
      this.curCode = value;
    },
  },
};
</script>
<style scoped lang="scss">
.container {
  width: 100%;
  height: 100%;
}

.homepage {
  width: 100%;
  height: calc(100% - 64px);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}
.asider-view {
  width: 242px;
  /* height: 100vh; */
  overflow-y: auto;
  background-color: #1a1a1a;
}

/* 修改滚动条样式 
侧边栏
contentview
*/
.homepage {
  .asider-view::-webkit-scrollbar,
  .component-view::-webkit-scrollbar {
    width: 10px;
    // height: 10px;
  }
  .asider-view::-webkit-scrollbar-track,
  .component-view::-webkit-scrollbar-track {
    background: rgb(239, 239, 239);
  }
  .asider-view::-webkit-scrollbar-thumb,
  .component-view::-webkit-scrollbar-thumb {
    background: #ddd;
    border-radius: 10px;
  }
  .asider-view::-webkit-scrollbar-thumb:hover,
  .component-view::-webkit-scrollbar-thumb:hover {
    background: #4c4c4c;
  }
}

/* content-view */
.content-view {
  /* flex: 1; */
  /* flex-direction: column; */
  width: calc(100% - 242px);
  height: 100%;
  display: flex;
}

.component-view {
  width: 60%;
  height: 100%;
  text-align: left;
  padding: 15px 30px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}
.component-view-title {
  padding: 20px;
  color: #333;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  text-shadow: 2px 2px #ddd;
}
.component-item {
  /* display: inline-block; */
  margin-top: 15px;
  cursor: pointer;
  /* overflow-x: auto; */
  border: 2px dashed transparent;
  padding: 15px;
  box-shadow: 0px 15px 10px -15px rgba(75, 75, 75, 0.1);
}
.component-active {
  border-color: #409eff;
}

.code-view {
  width: 40%;
  height: 100%;
}
/* attr */
.attr-view {
  flex: 2;
}
.attr-view h6 {
  line-height: 60px;
  border-bottom: 1px solid #999;
}
</style>
