<template>
  <div class="date-default">
    <div class="label">日期区间选择器</div>
    <div class="block">
        <el-date-picker
          v-model="date1"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          :picker-options="pickerOptions"
          prefix-icon="prefix-icon-class"
          end-placeholder="结束日期">
        </el-date-picker>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      },
      date1: "",
    };
  },
  mounted () {
    // console.log(666, document.getElementsByClassName('el-input__icon'));
    document.getElementsByClassName('el-input__icon')[2].classList.remove('el-range__close-icon')
    document.getElementsByClassName('el-input__icon')[2].classList.add('el-icon-date')
  },
};
</script>
<style scoped lang="scss">
.date-default {
    padding: 10px;
}
.date-default .label {
    text-align: left;
    line-height: 22px;
    margin-bottom: 8px;
    color: #000000;
}

// inner hover
:deep() .el-date-editor.el-input {
  background-color: #F2F2F2;
}

// inner左侧padding
:deep() .el-input--suffix .el-input__inner {
  padding-left: 10px;
}

// 左侧icon隐藏
:deep() .prefix-icon-class {
  display: none;
}

// 右侧icon
:deep() .suffix-icon-class::after {
  content: 'xxxx';
  font-size: 14px;
  color: '#000000';
  width: 15px;
  height: 15px;
}

:deep() .el-input__icon {
// 移动后的icon高度100%会造成不居中
  height: 40px;
}
</style>