export default "<template>\r\n  <div class=\"table-view\">\r\n    <div class=\"table-head\">\r\n      <div class=\"left\">\r\n        <div class=\"border\"></div>\r\n        奖励列表\r\n      </div>\r\n      <div class=\"right\">\r\n        <el-button\r\n          type=\"primary\"\r\n          class=\"new-button\"\r\n        >\r\n          <img class=\"icon\" src=\"@/assets/icon-a/export.png\" alt=\"\">\r\n          <span>导出</span>\r\n        </el-button>\r\n      </div>\r\n    </div>\r\n    <el-table\r\n      ref=\"multipleTable\"\r\n      :data=\"tableData\"\r\n      :border=\"false\"\r\n      stripe\r\n      style=\"width: 100%\"\r\n      :header-cell-style=\"{backgroundColor: '#EEF0F3', color:'#666666'}\"\r\n      @selection-change=\"handleSelectionChange\"\r\n    >\r\n      <el-table-column type=\"selection\" width=\"55\" align=\"center\"></el-table-column>\r\n      <el-table-column\r\n        v-for=\"item in columnData\"\r\n        :key=\"item.id\"\r\n        :fixed=\"item.fixed\"\r\n        :prop=\"item.prop\"\r\n        :label=\"item.label\"\r\n        :show-overflow-tooltip=\"item.tooltip\"\r\n        :width=\"item.width\"\r\n        :align=\"item.align\"\r\n        :header-align=\"item.headerAlign\"\r\n      >\r\n        <template slot-scope=\"scope\">\r\n          <!-- <template v-if=\"item.prop == 'buyType'\">\r\n            <el-tag\r\n              :type=\"scope.row.buyType === 1 ? 'primary' : 'success'\"\r\n              disable-transitions\r\n            >\r\n              {{ scope.row.buyType === 1 ? \"公司\" : \"个人\" }}\r\n            </el-tag>\r\n          </template> -->\r\n          <template v-if=\"item.prop == 'disabled'\">\r\n             <el-tag\r\n                :class=\"scope.row.disabled ? 'disabled-tag' : 'open-tag'\"\r\n                :color=\"scope.row.disabled ? '#FFF2E8' : '#F6FFED'\"\r\n              >\r\n              {{ scope.row.disabled ? \"禁用\" : \"可用\" }}\r\n              </el-tag>\r\n          </template>\r\n          <template v-else>{{ scope.row[item.prop] }}</template>\r\n        </template>\r\n      </el-table-column>\r\n\r\n      <el-table-column fixed=\"right\" label=\"操作\" width=\"140\" align=\"center\">\r\n        <template slot-scope=\"scope\">\r\n          <div class=\"edit-cell\">\r\n              <div class=\"tag check-tag\" @click=\"handleClick(scope.row)\"><span>查看</span></div>\r\n              <div class=\"tag detail-tag\" @click=\"handleClick(scope.row)\"><span>明细</span></div>\r\n             <!-- <el-tag\r\n                color=\"#EEF0F3\"\r\n                class=\"edit-tag\"\r\n                \r\n              >编辑</el-tag>\r\n              <el-tag\r\n                color=\"#EEF0F3\"\r\n                class=\"auth-tag\"\r\n              >\r\n                角色授权\r\n              </el-tag>\r\n              <el-tag\r\n                color=\"#EEF0F3\"\r\n                class=\"status-tag\"\r\n              >\r\n                修改状态\r\n              </el-tag> -->\r\n          </div>\r\n        </template>\r\n      </el-table-column>\r\n    </el-table>\r\n    <div class=\"table-foot\">\r\n      <el-pagination\r\n        background\r\n        layout=\"prev, pager, next\"\r\n        :total=\"total\"\r\n        @current-change=\"paginationChange\"\r\n        @prev-click=\"prevClick\"\r\n        @next-click=\"nextClick\"\r\n      ></el-pagination>\r\n      <div class=\"pagination-sup\">{{ pageSize }}/{{ total }}</div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      columnData: [\r\n        {\r\n          // fixed: true,\r\n          prop: \"name\",\r\n          width: \"150px\",\r\n          tooltip: true,\r\n          label: \"奖励名称\",\r\n          align: \"center\",\r\n          headerAlign: \"center\"\r\n        },\r\n        {\r\n          prop: \"code\",\r\n          width: \"150px\",\r\n          label: \"奖励编码\",\r\n          align: \"center\",\r\n          headerAlign: \"center\"\r\n        },\r\n       