\n        },\r\n        {\r\n          id: 13,\r\n          name: \"北京车展自制卡劵\",\r\n          address:\r\n            \"上海市普陀区金沙江路 1518 弄 上海市普陀区金沙江路 1518 弄\",\r\n          code: \"8956445641551\",\r\n          type: \"卡券奖励\",\r\n          date: \"2022-06-02\",\r\n          color: \"前进红\",\r\n          integral: \"66\",\r\n          disabled: false,\r\n          buyType: 2,\r\n        },\r\n        {\r\n          id: 14,\r\n          name: \"北京车展自制卡劵\",\r\n          address:\r\n            \"上海市普陀区金沙江路 1518 弄 上海市普陀区金沙江路 1518 弄\",\r\n          code: \"8866644564151\",\r\n          type: \"卡券奖励\",\r\n          date: \"2022-07-02\",\r\n          color: \"前进红\",\r\n          integral: \"88\",\r\n          disabled: false,\r\n          buyType: 1,\r\n        },\r\n      ],\r\n      multipleSelection: [],\r\n      pageSize: 10,\r\n      total: 156,\r\n    };\r\n  },\r\n\r\n  methods: {\r\n    handleClick(row) {\r\n      console.log(row);\r\n    },\r\n    handleSelectionChange(val) {\r\n      this.multipleSelection = val;\r\n    },\r\n    paginationChange(e) {\r\n      console.log(\"current改变\",e);\r\n    },\r\n    prevClick() {\r\n      console.log(\"上一页\");\r\n    },\r\n    nextClick() {\r\n      console.log(\"下一页\");\r\n    }\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.table-view {\r\n  width: 800px;\r\n  overflow: auto;\r\n  padding: 15px;\r\n}\r\n.table-view .table-head {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin-bottom: 10px;\r\n}\r\n.table-head .left {\r\n  font-weight: 500;\r\n  font-size: 16px;\r\n  color: #1a1a1a;\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n.table-head .left .border {\r\n  width: 4px;\r\n  height: 18px;\r\n  border-radius: 40px;\r\n  background-color: #f50537;\r\n  margin-right: 8px;\r\n}\r\n.table-head .right {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n}\r\n.table-head .new-button {\r\n  width: 112px;\r\n  height: 37px;\r\n  border: none;\r\n  background-color: #000000;\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  color: #ffffff;\r\n  border-radius: 2px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n.new-button .icon {\r\n  width: 11px;\r\n  height: 12px;\r\n  margin-right: 10px;\r\n  margin-bottom: 2px;\r\n}\r\n.new-button:active{\r\n  background: #252525;\r\n  color: #fff;\r\n  /* background: var(--el-button-hover-color);\r\n  border-color: var(--el-button-hover-color);\r\n  color: var(--el-button-font-color); */\r\n}\r\n\r\n\r\n/* checkbox */\r\n.table-view >>> .el-checkbox__input.is-checked .el-checkbox__inner {\r\n  background-color: #f50537;\r\n  border-color: #f50537;\r\n}\r\n.table-view >>> .el-checkbox__input.is-indeterminate .el-checkbox__inner {\r\n  background-color: #f50537;\r\n  border-color: #f50537;\r\n}\r\n:deep() .el-checkbox__inner {\r\n  width: 24px;\r\n  height: 24px;\r\n  border-radius: 2px;\r\n  font-size: 18px;\r\n  border-color: #dcdfe6;\r\n  background: #fff;\r\n}\r\n/* focus border-color */\r\n:deep() .el-checkbox__input.is-focus .el-checkbox__inner {\r\n  border-color: #f50537;\r\n}\r\n/* 勾选状态：对勾样式 */\r\n:deep()  .el-checkbox__input.is-checked .el-checkbox__inner,\r\n.el-checkbox__input.is-indeterminate .el-checkbox__inner {\r\n  background-color: #fff;\r\n  border-color: #fff;\r\n}\r\n:deep()  .el-checkbox__inner::after {\r\n  border: 2px solid #fff;\r\n  border-left: 0px;\r\n  border-top: 0px;\r\n  width: 5px;\r\n  height: 9px;\r\n  left: 8px;\r\n  top: 4px;\r\n  position: absolute;\r\n}\r\n/* 半选中的 checkbox 样式 */\r\n:deep() .is-indeterminate .el-checkbox__inner::before  {\r\n    position: absolute;\r\n    top: 10px;\r\n    border: 1px solid #fff;\r\n}\r\n\r\n:deep()  .el-checkbox__inner:hover {\r\n  border-color: #f50537;\r\n}\r\n      \r\n\r\n/* tag */\r\n.open-tag {\r\n  padding: 0 10px;\r\n  height: 24px;\r\n  line-height: 24px;\r\n  color: #52C41A;\r\n  border: 1px solid #B7EB8F;\r\n  border-radius: 2px;\r\n}\r\n.disabled-tag {\r\n  padding: 0 10px;\r\n  height: 24px;\r\n  line-height: 24px;\r\n  color: #FA541C;\r\n  border:1px solid #FFBB96;\r\n  border-radius: 2px;\r\n}\r\n\r\n.edit-cell {\r\n  white-space: nowrap;\r\n}\r\n.edit-cell .check-tag {\r\n  display: inline-block;\r\n  color: #3B72FE;\r\n  font-weight: 400;\r\n  font-size: 14px;\r\n  padding: 5px 0;\r\n}\r\n.edit-cell .check-tag span {\r\n  padding: 0px 10px;\r\n  border-right: 1px solid #C6D0DF;\r\n}\r\n.edit-cell .detail-tag {\r\n  display: inline-block;\r\n  color: #3B72FE;\r\n  font-weight: 400;\r\n  font-size: 14px;\r\n  padding: 5px 0;\r\n}\r\n.edit-cell .detail-tag span {\r\n  padding: 0px 10px;\r\n}\r\n.edit-cell .tag:hover {\r\n  opacity: 0.8;\r\n}\r\n/* .edit-cell .edit-tag{\r\n  height: 30px;\r\n  line-height: 30px;\r\n  color: #F50537;\r\n  border: none;\r\n  border-radius: 2px;\r\n}\r\n.edit-cell .auth-tag {\r\n  height: 30px;\r\n  line-height: 30px;\r\n  color: #35445A;\r\n  border: none;\r\n  border-radius: 2px;\r\n  margin-left: 10px;\r\n}\r\n.edit-cell .status-tag {\r\n  height: 30px;\r\n  line-height: 30px;\r\n  color: #7B899E;\r\n  border: none;\r\n  border-radius: 2px;\r\n  margin-left: 10px;\r\n} */\r\n\r\n/* footer */\r\n.table-foot {\r\n  margin-top: 8px;\r\n  display: flex;\r\n  justify-content: flex-end;\r\n}\r\n\r\n.table-foot >>> .el-pagination.is-background .btn-prev,\r\n.table-foot >>> .el-pagination.is-background .btn-next {\r\n  background-color: #ffffff;\r\n  border: 1px solid #e5e5e5;\r\n  border-radius: 2px;\r\n}\r\n.table-foot\r\n  >>> .el-pagination.is-background\r\n  .el-pager\r\n  li:not(.disabled).active {\r\n  background-color: #ffffff;\r\n}\r\n.table-foot >>> .el-pager li {\r\n  color: #1a1a1a;\r\n  background-color: #ffffff;\r\n  border: 1px solid #e5e5e5;\r\n  border-radius: 2px;\r\n}\r\n.table-foot >>> .el-pager li:hover {\r\n  color: #999999 !important;\r\n}\r\n.table-foot >>> .el-pager li:hover {\r\n  background-color: #ffffff;\r\n}\r\n\r\n.table-foot\r\n  >>> .el-pagination.is-background\r\n  .el-pager\r\n  li:not(.disabled).active {\r\n  color: #f50537;\r\n  border-color: #f50537;\r\n}\r\n.table-foot .pagination-sup {\r\n  width: 64px;\r\n  margin-top: 2px;\r\n  height: 28px;\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  color: #1a1a1a;\r\n  background-color: #ffffff;\r\n  border: 1px solid #e5e5e5;\r\n  border-radius: 2px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n/* 修改滚动条样式 */\r\n:deep() div::-webkit-scrollbar{\r\n    height:10px;\r\n}\r\n:deep() .el-table__body-wrapper::-webkit-scrollbar-thumb {\r\n  position: relative;\r\n  height: 10px!important;\r\n  background-color: #F2F2F2;\r\n  border-radius: 66px;\r\n}\r\n:deep() div::-webkit-scrollbar-thumb:hover{\r\n  background: #4C4C4C;\r\n}\r\n</style>\r\n";