<template>
     <el-form class="form-view" label-position="top" label-width="80px">
        <el-form-item label="基础输入框">
          <el-input
            class="default-input"
            placeholder="请输入"
            v-model="value"
          ></el-input>
        </el-form-item>
        <el-form-item label="前缀Icon">
          <el-input
            class="before-input"
            placeholder="请输入"
            v-model="valueIcon"
          >
          <img slot="prefix" class="prefix-icon" src="@/assets/icon-a/vector-s.png" alt="">
          </el-input>
        </el-form-item>
        <el-form-item label="后缀Icon">
          <el-input
            class="after-input"
            placeholder="请输入文字"
            v-model="valueAfter"
          >
            <img slot="suffix" class="suffix-icon" src="@/assets/icon-a/tip.png" alt="">
          </el-input>
        </el-form-item>
        <el-form-item label="前后置内容">
          <el-input
            class="pre-a-input"
            placeholder="请输入文字"
            v-model="valuePAVal"
          >
           <template slot="prepend">https://</template>
           <template slot="append">.com</template>
          </el-input>
        </el-form-item>
        <!-- <el-form-item label="可选择">
            <el-input
              class="select-input"
              placeholder="请输入文字"
              v-model="valueSL"
            >
            <template slot="prepend">https://</template>
             <el-select class="input-with-select" v-model="select" slot="append" placeholder="请选择">
                <el-option label=".com" value=".com"></el-option>
                <el-option label=".cn" value=".cn"></el-option>
                <el-option label=".net" value=".net"></el-option>
              </el-select>
            </el-input>
         </el-form-item> -->
         <el-form-item label="禁用">
            <el-input
              placeholder="请输入文字"
              disabled
              class="disabled-input"
            >
            </el-input>
          </el-form-item>
      </el-form>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      valueIcon: "",
      valueAfter: "",
      valuePAVal: "",
      valueSL: "",
      select: '.net',
    };
  },
};
</script>
<style scoped>
:deep() .el-form-item__label {
  padding: 0;
  color: #1A1A1A;
}
:deep() .form-view {
  padding: 20px;
  text-align: left;
}

/* 基础样式 */
:deep() .el-input__inner {
  color: #000000;
}
:deep() .el-input__inner:focus {
  border: 1px solid #f50537;
}
:deep() .el-input__inner:hover {
  background-color: #FFFFFF;
}

:deep() .el-input-group__prepend {
   background-color: #FFFFFF;
}
:deep() .el-input-group__append {
  background-color: #FFFFFF;
}

/* 基础输入框 */
.default-input {
  width: 230px;
  height: 37px;
}

/* 前置内容输入框 */
.before-input {
  width: 230px;
  /* padding-left: 10px; */
}
.prefix-icon {
  width: 14px;
  height: 14px;
  margin-left: 8px;
}
/* 后置内容输入框 */
.after-input {
   width: 230px;
}
.suffix-icon {
  width: 14px;
  height: 14px;
  margin-right: 8px;
}
/* 前后置内容输入框 */
.pre-a-input {
  width: 380px;
}

/* 可选择后缀输入框 */
.select-input {
  width: 380px;
}
.el-select .el-input {
    width: 130px;
}
.input-with-select {
  width: 90px;
}

/* 禁用输入框 */
.disabled-input {
  background: #FFFFFF;
  border-color:#D9D9D9;
  color: #D9D9D9;
}
</style>
