export default "<template>\r\n  <div>\r\n    <div class=\"select-label\">选项标题</div>\r\n    <el-select :popper-append-to-body=\"false\" v-model=\"value\" multiple :collapse-tags=\"false\" placeholder=\"请选择\">\r\n      <el-option\r\n        v-for=\"item in options\"\r\n        :key=\"item.value\"\r\n        :label=\"item.label\"\r\n        :value=\"item.value\"\r\n      >\r\n      \r\n      </el-option>\r\n    </el-select>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      options: [\r\n        { value: 1, label: \"选项一\" },\r\n        { value: 2, label: \"选项二\" },\r\n        { value: 3, label: \"选项三\" },\r\n        { value: 4, label: \"选项四\" },\r\n      ],\r\n      value: null,\r\n    };\r\n  },\r\n};\r\n</script>\r\n<style scoped lang=\"scss\">\r\n.select-label {\r\n    text-align: left; \r\n    font-weight: 400;\r\n    font-size: 14px;\r\n    line-height: 35px;  \r\n    color: #1A1A1A;\r\n    \r\n}\r\n/deep/ .is-focus .el-input__inner {\r\n  border-color: #f50537 !important;\r\n}\r\n/deep/ .el-input .el-input__inner {\r\n  border-color: #dcdfe6;\r\n}\r\n.el-input__inner {\r\n  color: #1a1a1a;\r\n}\r\n.el-select-dropdown__item {\r\n  color: #999999;\r\n}\r\n.el-select-dropdown__item .hover {\r\n  background-color: #ffffff;\r\n}\r\n\r\n /* 修改滚动条 */\r\n/deep/ .el-scrollbar__bar .el-scrollbar__thumb {\r\n  background-color: #4C4C4C;\r\n}\r\n\r\n/* 选中 */\r\n.el-select-dropdown.is-multiple .el-select-dropdown__item.selected {\r\n    color: #999999;\r\n    font-weight: 500;\r\n    background-color: #f9f9f9;\r\n}\r\n/* 去除对勾 */\r\n.el-select-dropdown.is-multiple .el-select-dropdown__item.selected::after {\r\n    content: '';\r\n}\r\n/* tag */\r\n/deep/ .el-tag.el-tag--info {\r\n    color: #1A1A1A!important;\r\n}\r\n</style>\r\n";