export default "<template>\r\n  <div style=\"margin: 15px\">\r\n    <button class=\"warn-button\">警示按钮</button>\r\n\r\n    <span style=\"margin-left: 50px\"></span>\r\n\r\n    <button class=\"warn-button disabled\">警示禁用</button>\r\n  </div>\r\n</template>\r\n<style scoped>\r\n.warn-button {\r\n  cursor: pointer;\r\n  border: none;\r\n  background-color: #f50537;\r\n  font-size: 14px;\r\n  color: #ffffff;\r\n  border-radius: 2px;\r\n  min-width: 112px;\r\n  height: 37px;\r\n}\r\n.warn-button:hover {\r\n  background-color: #eb0d3f;\r\n}\r\n\r\n.warn-button:active {\r\n  background-color: #fd2c4e;\r\n}\r\n\r\n/* 禁用 */\r\n.warn-button.disabled {\r\n  cursor: not-allowed;\r\n  background-color: #d9d9d9;\r\n  color: #999999;\r\n}\r\n</style>\r\n";