<template>
  <div class="navbox">
    <div class="logobox">
      <img class="logo" src="@/assets/logo.png" alt="logo" />
      <span class="title">规范设计</span>
    </div>
    <div class="middle"></div>
    <div class="userbox">
       <div class="help" @click="helpTap">
          使用帮助
          <img src="@/assets/icon/help.png" alt="">
       </div>
      <div class="userimg">
        <img :src="USER.avatar" alt="" />
      </div>
      <div class="username">{{ USER.name }}</div>
     
      <div class="signout pointer" @click.stop="dialogShow = true">退出</div>
    </div>

    <el-drawer
      :with-header="true"
      :visible.sync="helpDrawer"
      direction="ttb"
      size="100%"
    >
      <template slot="title">
        <div style="font-size: 16px; font-weight: 600">使用帮助</div>
      </template>
      <code-editor :value="codeString" language="markdown"></code-editor>
    </el-drawer>

    <default-dialog
      content="确定退出当前账户, 是否继续?"
      confirmText="确定"
      cencelText="取消"
      :dialogShow="dialogShow"
      @close="cancel"
      @confirm="logoutApi"
    ></default-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import defaultDialog from "@/mycoms/dialog/default.vue";
import codeEditor from "@/mycoms/code-editor.vue";
import helpCode from "!!raw-loader!./help.md";
export default {
  name: "HeaderBar",
  components: { defaultDialog, codeEditor },
  data() {
    return {
      logo: require("@/assets/logo.png"),
      dialogShow: false,
      helpDrawer: false,
    };
  },
  computed: {
    ...mapGetters("USER", {
      USER: "getUserInfo",
    }),
    codeString() {
      return helpCode || "";
    },
  },
  mounted() {
    console.log(666, this.USER);
  },
  methods: {
    ...mapActions("USER", ["setUserInfo", "setToken"]),
    cancel() {
      this.dialogShow = false;
      this.$message.info("取消退出");
    },
    async logoutApi() {
      try {
        localStorage.setItem("TOKEN", null);
        await this.setUserInfo("");
        await this.setToken("");
        this.dialogShow = false;
        this.$message.success("退出成功!");
        this.$router.push("/login");
      } catch (error) {
        console.log("logoutApi", error);
      }
    },
    helpTap() {
      this.helpDrawer = true;
    },
  },
};
</script>
<style scoped lang="scss">
.navbox {
  width: 100%;
  height: 64px;
  display: flex;
  flex-direction: row;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 4px 6px rgba(75, 75, 75, 0.1);
  .logobox {
    user-select: none;
    display: flex;
    align-items: center;
    padding: 0 16px;
    height: 100%;
    color: #000000;
    font-size: 14px;

    .logo {
      width: 83px;
      height: 28px;
    }

    .title {
      margin-left: 18px;
      font-weight: 600;
    }
  }
  .middle {
    flex: 1;
  }
  .userbox {
    padding: 0 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "PingFang SC";
    font-style: normal;
    font-weight: 400;
    
    .help {
      cursor: pointer;
      font-size: 11px;
      color: #888;
      margin-right: 42px;
      display: flex;
      align-items: center;

      img {
        width: 12px;
        height: 12px;
        margin-left: 3px;
      }
    }

    .userimg {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      box-shadow: 0px 0px 10px 4px rgba(75, 75, 75, 0.1);
      background: #000000;
      margin-right: 12px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
      }
    }
    .username {
      font-size: 12px;
      color: #1a1a1a;
      margin-right: 22px;
    }
    .signout {
      padding: 15px 0;
      font-size: 12px;
      font-weight: 600;
      color: #4c4c4c;
    }
  }
}
</style>
