export default "<template>\r\n     <el-form class=\"form-view\" label-position=\"top\" label-width=\"80px\">\r\n        <el-form-item label=\"基础输入框\">\r\n          <el-input\r\n            class=\"default-input\"\r\n            placeholder=\"请输入\"\r\n            v-model=\"value\"\r\n          ></el-input>\r\n        </el-form-item>\r\n        <el-form-item label=\"前缀Icon\">\r\n          <el-input\r\n            class=\"before-input\"\r\n            placeholder=\"请输入\"\r\n            v-model=\"valueIcon\"\r\n          >\r\n          <img slot=\"prefix\" class=\"prefix-icon\" src=\"@/assets/icon-a/vector-s.png\" alt=\"\">\r\n          </el-input>\r\n        </el-form-item>\r\n        <el-form-item label=\"后缀Icon\">\r\n          <el-input\r\n            class=\"after-input\"\r\n            placeholder=\"请输入文字\"\r\n            v-model=\"valueAfter\"\r\n          >\r\n            <img slot=\"suffix\" class=\"suffix-icon\" src=\"@/assets/icon-a/tip.png\" alt=\"\">\r\n          </el-input>\r\n        </el-form-item>\r\n        <el-form-item label=\"前后置内容\">\r\n          <el-input\r\n            class=\"pre-a-input\"\r\n            placeholder=\"请输入文字\"\r\n            v-model=\"valuePAVal\"\r\n          >\r\n           <template slot=\"prepend\">https://</template>\r\n           <template slot=\"append\">.com</template>\r\n          </el-input>\r\n        </el-form-item>\r\n        <!-- <el-form-item label=\"可选择\">\r\n            <el-input\r\n              class=\"select-input\"\r\n              placeholder=\"请输入文字\"\r\n              v-model=\"valueSL\"\r\n            >\r\n            <template slot=\"prepend\">https://</template>\r\n             <el-select class=\"input-with-select\" v-model=\"select\" slot=\"append\" placeholder=\"请选择\">\r\n                <el-option label=\".com\" value=\".com\"></el-option>\r\n                <el-option label=\".cn\" value=\".cn\"></el-option>\r\n                <el-option label=\".net\" value=\".net\"></el-option>\r\n              </el-select>\r\n            </el-input>\r\n         </el-form-item> -->\r\n         <el-form-item label=\"禁用\">\r\n            <el-input\r\n              placeholder=\"请输入文字\"\r\n              disabled\r\n              class=\"disabled-input\"\r\n            >\r\n            </el-input>\r\n          </el-form-item>\r\n      </el-form>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      value: \"\",\r\n      valueIcon: \"\",\r\n      valueAfter: \"\",\r\n      valuePAVal: \"\",\r\n      valueSL: \"\",\r\n      select: '.net',\r\n    };\r\n  },\r\n};\r\n</script>\r\n<style scoped>\r\n:deep() .el-form-item__label {\r\n  padding: 0;\r\n  color: #1A1A1A;\r\n}\r\n:deep() .form-view {\r\n  padding: 20px;\r\n  text-align: left;\r\n}\r\n\r\n/* 基础样式 */\r\n:deep() .el-input__inner {\r\n  color: #000000;\r\n}\r\n:deep() .el-input__inner:focus {\r\n  border: 1px solid #f50537;\r\n}\r\n:deep() .el-input__inner:hover {\r\n  background-color: #FFFFFF;\r\n}\r\n\r\n:deep() .el-input-group__prepend {\r\n   background-color: #FFFFFF;\r\n}\r\n:deep() .el-input-group__append {\r\n  background-color: #FFFFFF;\r\n}\r\n\r\n/* 基础输入框 */\r\n.default-input {\r\n  width: 230px;\r\n  height: 37px;\r\n}\r\n\r\n/* 前置内容输入框 */\r\n.before-input {\r\n  width: 230px;\r\n  /* padding-left: 10px; */\r\n}\r\n.prefix-icon {\r\n  width: 14px;\r\n  height: 14px;\r\n  margin-left: 8px;\r\n}\r\n/* 后置内容输入框 */\r\n.after-input {\r\n   width: 230px;\r\n}\r\n.suffix-icon {\r\n  width: 14px;\r\n  height: 14px;\r\n  margin-right: 8px;\r\n}\r\n/* 前后置内容输入框 */\r\n.pre-a-input {\r\n  width: 380px;\r\n}\r\n\r\n/* 可选择后缀输入框 */\r\n.select-input {\r\n  width: 380px;\r\n}\r\n.el-select .el-input {\r\n    width: 130px;\r\n}\r\n.input-with-select {\r\n  width: 90px;\r\n}\r\n\r\n/* 禁用输入框 */\r\n.disabled-input {\r\n  background: #FFFFFF;\r\n  border-color:#D9D9D9;\r\n  color: #D9D9D9;\r\n}\r\n</style>\r\n";