export default "<template>\r\n  <div class=\"date-default\">\r\n    <div class=\"label\">默认日期选择器</div>\r\n    <el-date-picker\r\n      v-model=\"date1\"\r\n      type=\"date\"\r\n      placeholder=\"选择日期\"\r\n      :picker-options=\"pickerOptions\"\r\n      prefix-icon=\"prefix-icon-class\"\r\n    >\r\n      <i slot=\"suffix\" class=\"el-input__icon el-icon-date\"></i>\r\n    </el-date-picker>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      pickerOptions: {\r\n        disabledDate(time) {\r\n          return time.getTime() > Date.now();\r\n        },\r\n        shortcuts: [\r\n          {\r\n            text: \"今天\",\r\n            onClick(picker) {\r\n              picker.$emit(\"pick\", new Date());\r\n            },\r\n          },\r\n        //   {\r\n        //     text: \"昨天\",\r\n        //     onClick(picker) {\r\n        //       const date = new Date();\r\n        //       date.setTime(date.getTime() - 3600 * 1000 * 24);\r\n        //       picker.$emit(\"pick\", date);\r\n        //     },\r\n        //   },\r\n        //   {\r\n        //     text: \"一周前\",\r\n        //     onClick(picker) {\r\n        //       const date = new Date();\r\n        //       date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);\r\n        //       picker.$emit(\"pick\", date);\r\n        //     },\r\n        //   },\r\n        ],\r\n      },\r\n      date1: \"\",\r\n    };\r\n  },\r\n  mounted () {\r\n    document.getElementsByClassName('el-input__icon')[2].classList.remove('el-range__close-icon')\r\n    document.getElementsByClassName('el-input__icon')[2].classList.add('el-icon-date')\r\n  },\r\n};\r\n</script>\r\n<style scoped lang=\"scss\">\r\n.date-default {\r\n    padding: 10px;\r\n}\r\n.date-default .label {\r\n    text-align: left;\r\n    line-height: 22px;\r\n    margin-bottom: 8px;\r\n    color: #000000;\r\n}\r\n\r\n// inner hover\r\n:deep() .el-date-editor.el-input {\r\n  background-color: #F2F2F2;\r\n}\r\n\r\n// inner左侧padding\r\n:deep() .el-input--suffix .el-input__inner {\r\n  padding-left: 10px;\r\n}\r\n\r\n// 左侧icon隐藏\r\n:deep() .prefix-icon-class {\r\n  display: none;\r\n}\r\n\r\n// 右侧icon\r\n:deep() .suffix-icon-class::after {\r\n  content: 'xxxx';\r\n  font-size: 14px;\r\n  color: '#000000';\r\n  width: 15px;\r\n  height: 15px;\r\n}\r\n\r\n:deep() .el-input__icon {\r\n  // 移动后的icon高度100%会造成不居中\r\n  height: 40px;\r\n}\r\n</style>\r\n\r\n\r\n<!-- \r\n<template>\r\n  <div class=\"picker-root\">\r\n     <a-date-picker v-model=\"value1\" size=\"large\" placeholder=\"请选择日期\" >\r\n       <template #suffixIcon>\r\n          <img class=\"suffix-date\" src=\"@/assets/icon-a/date.png\" alt=\"icon\">\r\n      </template>\r\n     </a-date-picker>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  data(){\r\n      return {\r\n        value1: \"\",\r\n      };\r\n  }\r\n}\r\n\r\n</script>\r\n<style scoped lang=\"scss\">\r\n.picker-root {\r\n  padding: 10px;\r\n}\r\n:deep() .ant-input {\r\n  min-width: 217px;\r\n  font-size: 14px;\r\n}\r\n</style> -->\r\n";