export default "<template>\r\n  <div class=\"flex\">\r\n    <div class=\"flex\" @click.stop=\"radioChange(1)\">\r\n        <label>选项一</label>\r\n        <img class=\"radio\" :src=\"value == 1 ? radioActive : radio\" alt=\"\">\r\n    </div>\r\n    <div class=\"flex\" @click.stop=\"radioChange(2)\">\r\n        <label>选项二</label>\r\n        <img class=\"radio\" :src=\"value == 2 ? radioActive : radio\" alt=\"\">\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      radio: require('@/assets/icon/radio.png'),\r\n      radioActive: require('@/assets/icon/radio-active.png'),\r\n      value: '1',\r\n    };\r\n  },\r\n  methods: {\r\n    radioChange(index) {\r\n        this.value = index\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.flex {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\nlabel {\r\n    margin:0 5px 0 20px;\r\n}\r\n\r\n.radio {\r\n    width: 16px;\r\n    height: 16px;\r\n}\r\n</style>\r\n";