export default "<template>\r\n  <div style=\"margin: 15px\">\r\n    <button class=\"plain-button\"> 无边框按钮 </button>\r\n\r\n    <span style=\"margin-left: 50px\"></span>\r\n\r\n    <button class=\"plain-button disabled\">无边框按钮</button>\r\n  </div>\r\n</template>\r\n<style scoped>\r\n.plain-button {\r\n  cursor: pointer;\r\n  border: none;\r\n  background-color: #ffffff;\r\n  font-size: 14px;\r\n  color: #000000;\r\n  border-radius: 2px;\r\n  min-width: 112px;\r\n  height: 37px;\r\n}\r\n.plain-button:active {\r\n  color: #999999;\r\n}\r\n\r\n.plain-button:hover {\r\n  color: #666666;\r\n}\r\n\r\n/* 禁用 */\r\n.plain-button.disabled {\r\n  cursor: not-allowed;\r\n  border-color: #e5e5e5;\r\n  color: #808080;\r\n}\r\n</style>\r\n";