import Vue from "vue";
import VueRouter from "vue-router";
import Store from "@/store/index";
import homePage from "@/views/homepage/index.vue";

Vue.use(VueRouter);
const routes = [
  {
    path: "/home",
    name: "home",
    component: () => import("@/views/home/index.vue"),
    meta: {
      title: "首页",
      login: true,
    },
  },
  {
    path: "/homepage",
    name: "homepage",
    component: homePage,
    meta: {
      title: "首页",
      login: true,
    },
  },
  {
    path: "/",
    name: "/",
    component: homePage,
    meta: {
      title: "首页",
      login: true,
    },
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/login/index.vue"),
    meta: {
      title: "登录",
      login: false,
    },
  },
];

const router = new VueRouter({
  mode: "hash",
  routes,
});


const checkAuth = () => {
  let token = Store.getters["USER/getToken"];
  // return process.env.NODE_ENV === "development" || token;
  return token
};

router.beforeEach((to, from, next) => {
  console.log('---',to);
  if (to.meta && to.meta.login) {
    if (checkAuth(to)) {
      next();
    } else {
      next("/login");
    }
  } else {
    next();
  }
});

export default router;
