export default "<template>\r\n  <div class=\"pagination\">\r\n    <el-pagination\r\n      background\r\n      layout=\"prev, pager, next\"\r\n      :total=\"total\"\r\n      @current-change=\"paginationChange\"\r\n      @prev-click=\"prevClick\"\r\n      @next-click=\"nextClick\"\r\n    ></el-pagination>\r\n    <div class=\"pagination-sup\">{{ pageSize }}/{{ total }}</div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      pageSize: 10,\r\n      total: 156,\r\n    };\r\n  },\r\n\r\n  methods: {\r\n    paginationChange(e) {\r\n      this.$message.info(e);\r\n      console.log(\"current改变\", e);\r\n    },\r\n    prevClick() {\r\n      console.log(\"上一页\");\r\n    },\r\n    nextClick() {\r\n      console.log(\"下一页\");\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.pagination {\r\n  margin-top: 8px;\r\n  display: flex;\r\n  justify-content: flex-end;\r\n}\r\n.pagination >>> .el-pagination.is-background .btn-prev,\r\n.pagination >>> .el-pagination.is-background .btn-next {\r\n  background-color: #ffffff;\r\n  border: 1px solid #e5e5e5;\r\n  border-radius: 2px;\r\n}\r\n.pagination\r\n  >>> .el-pagination.is-background\r\n  .el-pager\r\n  li:not(.disabled).active {\r\n  background-color: #ffffff;\r\n}\r\n.pagination >>> .el-pager li {\r\n  color: #1a1a1a;\r\n  background-color: #ffffff;\r\n  border: 1px solid #e5e5e5;\r\n  border-radius: 2px;\r\n}\r\n.pagination >>> .el-pager li:hover {\r\n  color: #999999 !important;\r\n}\r\n.pagination >>> .el-pager li:hover {\r\n  background-color: #ffffff;\r\n}\r\n.pagination\r\n  >>> .el-pagination.is-background\r\n  .el-pager\r\n  li:not(.disabled).active {\r\n  color: #f50537;\r\n  border-color: #f50537;\r\n}\r\n.pagination .pagination-sup {\r\n  width: 64px;\r\n  margin-top: 2px;\r\n  height: 28px;\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  color: #1a1a1a;\r\n  background-color: #ffffff;\r\n  border: 1px solid #e5e5e5;\r\n  border-radius: 2px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n</style>\r\n";