import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// import 'element-ui/lib/theme-chalk/base.css'; // el-transition fade/zoom 等
// import './element-variables.scss'; //el 主题色更改


import './plugins/antDesign.js' // ant组件
