export default "<template>\r\n  <div>\r\n    <div class=\"select-label\">可选颜色</div>\r\n    <el-select\r\n      :popper-append-to-body=\"false\"\r\n      v-model=\"value\"\r\n      placeholder=\"请选择\"\r\n    >\r\n      <el-option\r\n        v-for=\"item in options\"\r\n        :key=\"item.value\"\r\n        :label=\"item.label\"\r\n        :value=\"item.value\"\r\n      >\r\n      </el-option>\r\n    </el-select>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      options: [\r\n        { value: 1, label: \"黑色\" },\r\n        { value: 2, label: \"渐变色\" },\r\n        { value: 3, label: \"前进红\" },\r\n        { value: 4, label: \"渐变黑\" },\r\n        { value: 5, label: \"粉色\" },\r\n        { value: 6, label: \"蓝色\" },\r\n        { value: 7, label: \"金属色\" },\r\n        { value: 8, label: \"白色\" },\r\n        { value: 9, label: \"灰白色\" },\r\n        { value: 10, label: \"天空蓝\" },\r\n        { value: 11, label: \"红色\" },\r\n        { value: 12, label: \"紫色\" },\r\n      ],\r\n      value: null,\r\n    };\r\n  },\r\n};\r\n</script>\r\n<style scoped>\r\n.select-label {\r\n  text-align: left;\r\n  font-weight: 400;\r\n  font-size: 14px;\r\n  line-height: 35px;\r\n  color: #1a1a1a;\r\n}\r\n/* 修改滚动条 */\r\n:deep() .el-scrollbar__bar .el-scrollbar__thumb {\r\n  background-color: #4c4c4c;\r\n}\r\n:deep() .is-focus .el-input__inner {\r\n  border-color: #f50537 !important;\r\n}\r\n:deep().el-input .el-input__inner {\r\n  border-color: #dcdfe6;\r\n}\r\n.el-input__inner {\r\n  color: #1a1a1a;\r\n}\r\n.el-select-dropdown__item {\r\n  color: #999999;\r\n}\r\n.el-select-dropdown__item .selected {\r\n  color: #999999;\r\n  background-color: #f9f9f9;\r\n}\r\n.el-select-dropdown__item .hover {\r\n  background-color: #ffffff;\r\n}\r\n</style>\r\n";